<template>
	<div class="Details">
		<van-nav-bar fixed title="商品详情" @click-left="goback" left-text="返回" left-arrow></van-nav-bar>
		<div class="top-box">
			<img class="auto-img" :src="getProductDetails.large_img">
			<div class="tipsText">
				<div class="name">{{getProductDetails.name}}({{getProductDetails.enname}})</div>
				<div class="desc">{{getProductDetails.desc}}</div>
			</div>
		</div>
		<div class="choice">
			<div class="price">
				<p class="types">品类 : 
				<span class="title">{{getProductDetails.type_desc}}({{getProductDetails.type}})</span></p>
				<p class="prices">价格 : 
				<span class="title">￥{{getProductDetails.price}}</span></p>
			</div>
			<div class="appropriate">
				<span class="appflavor">选择您的口味</span>
				<span class="appTips">单选</span>
			</div>

			<div class="flavor" v-for="(item,index) in radioData" :key="index">
				<p class="flavorChoice publics">{{item.title}}</p>
				<van-radio-group v-model="radios[index]">
					<van-radio name="1" ref="text1" checked-color="#21b276" 
					:value='item.option.tem1' icon-size="15px">{{item.option.tem1}}</van-radio>
					<van-radio name="2" ref="text1" checked-color="#21b276" 
					:value='item.option.tem2' icon-size="15px">{{item.option.tem2}}</van-radio>
					<van-radio name="3" ref="text1" checked-color="#21b276" 
					:value='item.option.tem3' icon-size="15px">{{item.option.tem3}}</van-radio>
				</van-radio-group>
			</div>

			<div class="buyNum">
				<div class="select-count">购买数量</div>
				<div class="count">
					<van-stepper v-model="count" theme="round" button-size="19" disable-input />
				</div>
			</div>
		</div>
		<div class="addBuy">
			<van-goods-action>
				<van-goods-action-icon icon="bag" 
				:badge="shopbagCount>0?shopbagCount:''" @click="goShopbag" color="#999" text="购物袋" />
				<van-goods-action-icon icon="like" 
				:color="likeStatus?'#21b276':'#999'" @click="likeOrNotLike" :text="likeStatus?'已收藏':'收藏'" />
				<van-goods-action-button color="#82d8b1" type="warning" text="加入购物车" @click="addShopCar" />
				<van-goods-action-button color="#21b276" type="danger" text="立即购买" @click="nowPay"/>
			</van-goods-action>
		</div>

	</div>
</template>

<script>
	import "../assets/less/ProductDetails.less"
	import{mapState,mapMutations} from "vuex";
	export default {
		name: "ProductDetails",
		data() {
			return {
				getProductDetails: {},
				radios: [],
				pid: "",
				// 购买商品数量
				count: 1,
				tokenString: "",
				//收藏状态
				likeStatus: false,
				
				// //购物袋提示数(已经在vuex中定义全局变量)
				// shopbagCount:0,
				//可选口味
				radioData: [{
						title: "饮品温度",
						option: {
							tem1: "冷",
							tem2: "常温",
							tem3: "热"
						}
					},
					{
						title: "饮品甜度",
						option: {
							tem1: "无糖",
							tem2: "半糖",
							tem3: "全糖"
						}
					},
					{
						title: "饮品附加",
						option: {
							tem1: "奶",
							tem2: "奶油",
							tem3: "芝士"
						}
					}
				],
				//最终口味数据
				flavorData: '',

			}
		},
		created() {
			this.pid = this.$route.params.pid;
			this.getDetails()
			this.getLikeProduct()
			this.getShopbagCount()
			//截取pid
		},
		computed:{
			...mapState(["shopbagCount","isLoadShopbagCount"]),
		},
		methods: {
			//解构vueX中的mutations函数
			...mapMutations(["changeShopbagCount","changeIsLoadShopbagCount"]),
			
			//返回上一级=============
			goback() {
				// this.$router.push({name:"Main"})
				this.$router.back();
			},
			//跳转到购物车页面
			goShopbag(){
				this.$router.push({name:"Shopbag"})
			},
			// 获取单选框的口味数据===========
			getFlavorData() {
				this.flavorData = "";
				let radioHtml = this.$refs.text1
				for (let i = 0; i < radioHtml.length; i++) {
					if (radioHtml[i].checked) {
						this.flavorData += "/" + radioHtml[i].value
					}
				}
				this.flavorData = this.flavorData.slice(1)
				// console.log(this.flavorData);

			},

			// 获取商品详情==========
			getDetails() {
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.axios({
					method: 'GET',
					url: '/productDetail',
					params: {
						pid: this.pid
					}
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
					// console.log(res);
					if (res.data.code == 600) {
						this.getProductDetails = res.data.result[0]
						// console.log(this.getProductDetails);

						//数据处理

					} else {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						})
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},

			//收藏或者取消收藏
			likeOrNotLike() {
				if (this.likeStatus) {
					//取消收藏
					this.notLike();
				} else {
					//收藏
					this.like();
				}
			},

			//查询当前账户的收藏商品========
			getLikeProduct() {
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return;
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.axios({
					method: 'GET',
					url: '/findlike',
					params: {
						pid: this.pid,
						tokenString: this.tokenString

					}
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
					// console.log(res);
					if (res.data.code == 700) {
						return

					}
					if (res.data.code == 1000) {
						if (res.data.result.length > 0) {
							this.likeStatus = true
						}

					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},

			//收藏==========
			like() {
				this.tokenString = this.$cookies.get("tokenString");
				// console.log(this.tokenString);
				if (!this.tokenString) {
					console.log("没有token值");
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.axios({
					method: 'POST',
					url: '/like',
					data: {
						pid: this.pid,
						tokenString: this.tokenString
					}
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
					// console.log(res);
					if (res.data.code == 700) {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						});
						return this.$router.push({
							name: "Login"
						})

					}
					if (res.data.code == 800) {
						this.likeStatus = true
					} else {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						})
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},

			//取消收藏==========
			notLike() {
				this.tokenString = this.$cookies.get("tokenString");
				console.log(this.tokenString);
				// console.log(this.pid);
				if (!this.tokenString) {
					console.log("没有token值");
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.axios({
					method: 'POST',
					url: '/notlike',
					data: {
						pid: this.pid,
						tokenString: this.tokenString

					}
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
					console.log(res);
					if (res.data.code == 700) {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						});
						return this.$router.push({
							name: "Login"
						})

					}
					if (res.data.code == 900) {
						if (res.data.result === 1) {
							this.likeStatus = false
						}

					} else {
						this.$toast({
							message: res.data.msg,
							forbidClick: true,
							duration: 500
						})
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},
			//加入购物车========
			addShopCar() {
				this.tokenString = this.$cookies.get("tokenString");
				// console.log(this.tokenString);
				//如果没有登录
				if (!this.tokenString) {
					console.log("没有token值");
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.getFlavorData();
				if (!this.flavorData) {
					this.$toast({
						message: "请选择您的口味",
						forbidClick: true,
						duration: 500
					});
					return;
				}
				this.axios({
					method: 'POST',
					url: '/addShopcart',
					data: {
						pid: this.pid,
						tokenString: this.tokenString,
						count: this.count,
						rule:this.flavorData
					}
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
						// console.log(res);
						if (res.data.code == 700) {
							this.$toast({
								message: res.data.msg,
								forbidClick: true,
								duration: 500
							});
							return this.$router.push({
								name: "Login"
							})

						}
						if (res.data.code === 3000) {
							if (res.data.status===1) {
								//提交mutations修改购物袋的数量
								this.changeShopbagCount(this.shopbagCount+1)
								this.$toast({
									message: res.data.msg,
									forbidClick: true,
									duration: 500
								})
							}
						} else {
							this.$toast({
								message: res.data.msg,
								forbidClick: true,
								duration: 500
							})
						}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},
			//获取购物袋提示数据
			getShopbagCount(){
				if (this.isLoadShopbagCount) {
					console.log("已经加载过购物袋数量");
					return;
				}
				//获取用户token值
				let tokenString=this.$cookies.get("tokenString");
				
				//如果token为空,说明用户没有登录或未注册
				if (!tokenString) {
					console.log("没有token值");
					return
				}
				this.$toast.loading({
					message:"加载中...",
					forbidClick:true,
					duration:500,
					
				});
				//成功则请求
				this.axios({
					method:"GET",
					url:"/shopcartRows",
					params:{
						tokenString:tokenString
					}
				}).then((res)=>{
					// console.log("res==>",res);
					// 清除登录账户密码
					this.$toast.clear();
					
					//如果没有登录
					if (res.data.code===700) {
						return;
					}
					if (res.data.code===8000) {
						//说明加入成功,改变购物袋显示数字
						this.changeShopbagCount(res.data.result);
						this.changeIsLoadShopbagCount(true);
					}
				}).catch((err)=>{
					this.$toast.clear();
					console.log("err==>",err);
				})
			},
			
			//立即购买
			nowPay(){
				this.tokenString = this.$cookies.get("tokenString");
				// console.log(this.tokenString);
				//如果没有登录
				if (!this.tokenString) {
					console.log("没有token值");
					return this.$router.push({
						name: "Login"
					})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.getFlavorData();
				if (!this.flavorData) {
					this.$toast({
						message: "请选择您的口味",
						forbidClick: true,
						duration: 500
					});
					return;
				}
				this.axios({
					method: 'POST',
					url: '/addShopcart',
					data: {
						pid: this.pid,
						tokenString: this.tokenString,
						count: this.count,
						rule:this.flavorData
					}
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					// 	//如果请求成功清掉提示
						// console.log(res);
						if (res.data.code == 700) {
							this.$toast({
								message: res.data.msg,
								forbidClick: true,
								duration: 500
							});
							return this.$router.push({
								name: "Login"
							})
				
						}
						if (res.data.code === 3000) {
							if (res.data.status===1) {
								//提交mutations修改购物袋的数量
								this.$router.push({name:"OrderClosing",query:{sids:res.data.sid}})
								
							}
						} 
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			}
		}
	}
</script>
